.gallery {
  text-align: center;
}

.gallery h3 {
  font-family: $font-head;
  font-style: normal;
  font-weight: bold;
  font-size: 34px;
  line-height: 44px;
  margin-top: 0;
  padding: 0;
  margin-bottom: 31px;
  letter-spacing: 0.03em;
}

.gallery__follow {
  font-family: $font-text;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 21px;
  color: $text-color;
  margin: 0 auto;
  position: relative;

  &:hover {
    opacity: 0.5;
  }

  &::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 0px;
    border: 1px solid #a87b62;
    top: 25px;
    left: 0;
  }
}

.gallery__images {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0 auto;
  margin-top: 65px;

  @media (max-width: 1365px) {
    justify-content: center;
  }
}

.gallery__block {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-right: 27px;
  height: 418px;

  @media (max-width: 1365px) {
    justify-content: center;
  }

  &:last-child {
    margin-right: 0;
  }


}

.gallery__block-wrapper {
  display: flex;
  justify-content: space-between;
  min-width: 487px;

  @media (max-width: 1365px) {
    justify-content: center;
    margin-top: 27px;
  }
}

@media (max-width: $tablet-width) {
  .gallery {
  }
  .gallery__follow {
  }
  .gallery__images {
    justify-content: space-between;
  }
  .gallery__block-custom1 {
    width: 133px;
    height: 133px;
  }

  .gallery__block2 img {
    width: 231px;
    height: 240px;
  }

  .gallery__block-custom2 {
    width: 133px;
    height: 87px;
  }

  .gallery__block-wrapper {
    display: flex;
    justify-content: space-between;
    min-width: 282px;
    margin-top: 0;
  }
}

@media (max-width: $mobile-width) {
  .gallery {
  }
  .gallery__follow {
  }
  .gallery__images {
    margin-left: auto;
    margin-right: auto;
    max-width: 290px;
    flex-wrap: wrap;
  }
  .gallery__block-custom1 {
  }

  .gallery__block2 img {
  }

  .gallery__block {
    &:last-child {
      display: none;
    }

    &:nth-child(3n) {
      flex-direction: row;
    }
  }

  .gallery__block-custom2 {
  }
}
